body {
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.container {
  background-color: #fff;
  padding: 10%;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 30vw; /* Set a fixed width */
  height: 50vh; /* Set a fixed height */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
}

h2 {
  margin-bottom: 20px;
  text-align: center;
}

.input-group {
  margin-bottom: 15px;
  width: 90%;
}

.input-group label {
  display: block;
  margin-bottom: 5px;
}

.input-group input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.arrow-button,
.submit-button,
.back-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
  width: 100%; /* Ensure buttons take the full width */
  text-align: center; /* Center the button text */
  margin-bottom: 10px; /* Add space between buttons */
}

.submit-button{
}

.back-button {
  position: absolute;
  bottom: 20vh; /* Distance from the bottom */
  right: 60vw; /* Distance from the right */
  width: auto; /* Auto width to fit text */
  background-color: #d5d6d8;
  color: black;
}

.arrow-button:hover,
.submit-button:hover {
  background-color: #0056b3;
}

.back-button:hover{
  background-color: #99999a;
}

.arrow-button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
}
